<template>
  <a-modal 
    :maskClosable="false" 
    :title="$t('common.detail')" 
    :width="1000" 
    v-model="isShow"
    @ok="onSubmit"
    @cancel="cancel" 
    :destroyOnClose="true">
    <template slot="footer">
      <a-button key="back" @click="cancel" >{{ this.$t('common.btn_cancel') }}</a-button>
    </template>
    <div class="table-page-search-wrapper">
      <a-form class="ant-advanced-search-form searchFixed" >
        <a-row :gutter="48">
          <a-col :md="9" :sm="24">
            <a-form-item :label="$t('dataedit.device_num')" >
              <a-input v-model="queryParam.deviceNum"/>
            </a-form-item>
          </a-col>
          <a-col :md="9" :sm="24">
            <a-form-item :label="$t('device.index2')" >
              <a-input v-model="queryParam.description" />
            </a-form-item>
          </a-col>
          <a-col :md="4" :sm="48" :offset="1">
            <span class="table-page-search-submitButtons">
              <btn-search @click="onSearch" :showInput="false"></btn-search>
            </span>
          </a-col>
        </a-row>
      </a-form>
      <s-table  
        ref="table"
        :bordered="false"
        :autoLoad="true"
        :data="loadData"
        size="small"
        rowKey="id"
      >
        <a-table-column
          :title="this.$t('dataedit.device_num')"
          width="150px"
          dataIndex="deviceNum"
        ></a-table-column>
        <a-table-column 
          :title="this.$t('device.index2')"
          width="150px"
          dataIndex="meterName"
        >
        </a-table-column>
        <a-table-column
          :title="this.$t('loss.supkwh')"
          width="150px"
          dataIndex="kwhPaPtct"
        >
          <span slot-scope="text,record" >
            <span :style="{ color: record.color1}" :title="record.title1">{{ dire==0?record.kwhPaPtct:record.kwhRaPtct }}</span>
          </span>
        </a-table-column>
        <a-table-column
          :title="this.$t('device.dire')"
          width="150px"
        >
          <span slot-scope="text" >
            <span>{{ $t('loss.dire'+dire) }}</span>
          </span>
        </a-table-column>
        <a-table-column
          :title="this.$t('common.status')"
          width="150px"
          dataIndex="remark"
        >
        </a-table-column>
      </s-table>
    </div>
  </a-modal>
</template>

<script>
  import STable from '@/components/table/'
  import Dict from '@/views/public/Dict'
  import {
    axios
  } from '@/utils/request'
  export default {
    components: {
      STable,
      Dict,
      
    },
    data() {
      return {
        data: {},
        isShow: false,
        dire:0,
        params:{},
        queryParam: {},
        loadData: (parameter) => {  
          const that = this 
          that.queryParam.dataType=that.params.dataType  
          that.queryParam.refId=that.params.refId   
          that.queryParam.nowTime=that.params.nowTime   
          that.queryParam.way=that.params.way   
          that.queryParam.supplyDire=that.dire   
          return axios({
            method: 'get',
            url: '/loss/report/four/search-detail',
            params: Object.assign(parameter, this.queryParam)
          }).then(res => {
            for(var i=0;i<res.result.data.length;i++){
              var row=res.result.data[i]
              if(row.veeEstmtDetail){
                var estmData=JSON.parse(row.veeEstmtDetail)
                estmData.data.forEach(item => {
                  if(this.params.dataType =='0'){
                    if(item.result=='S' && item['VEE00012']){
                      res.result.data[i].color1='#2bce2b'
                      res.result.data[i].title1=this.$t('dataedit.index3')
                    }
                    if(item.result=='F' && item['VEE00012']){
                      if(this.dire==0){
                        res.result.data[i].kwhPaPtct=''
                      }else{
                        res.result.data[i].kwhRaPtct=''
                      }
                    }
                  }else if(this.params.dataType =='1'){
                    if(item.result=='S' && item['VEE00112']){
                      res.result.data[i].color1='#2bce2b'
                      res.result.data[i].title1=this.$t('dataedit.index3')
                    }
                    if(item.result=='F' && item['VEE00112']){
                      if(this.dire==0){
                        res.result.data[i].kwhPaPtct=''
                      }else{
                        res.result.data[i].kwhRaPtct=''
                      }
                    }
                  }
                })
              }
              if(row.veeEditDetail){
                var editData=JSON.parse(row.veeEditDetail)
                editData.data.forEach(item => {
                  if(this.params.dataType =='0'){
                    if(item['VEE00012']){
                      res.result.data[i].color1='#39B1E9'
                      res.result.data[i].title1=this.$t('dataedit.index41')
                    }
                  }else if(this.params.dataType =='1'){
                    if(item['VEE00112']){
                      res.result.data[i].color1='#39B1E9'
                      res.result.data[i].title1=this.$t('dataedit.index41')
                    }
                  }
                })
              }
              var file
              if(this.params.dataType =='0'){
                 if(this.dire==0){
                    file='VEE30012'
                 }else{
                    file='VEE40012'
                 }
              }else{
                 if(this.dire==0){
                    file='VEE30112'
                 }else{
                    file='VEE40112'
                 }
              }
              var value0=false
              var svalue=false
              var lvalue=false
              if(row.veeResultDetail){
                var resultData=JSON.parse(row.veeResultDetail)
                resultData.data.forEach(item => {
                  if(item[file]){
                      for(var j=0;j<item[file].length;j++){
                        var row1=item[file][j]
                        if(row1['id']=='R005'){
                            if(row1['result']=='1'){
                                value0=true
                            }
                        }else if(row1['id']=='R006' || row1['id']=='R007'){
                              if(row1['result']=='1'){
                                svalue=true
                              }
                        }else if(row1['id']=='R009' || row1['id']=='R008'){
                              if(row1['result']=='1'){
                                lvalue=true
                              }
                        }
                    }
                  }
                })
              }
              res.result.data[i].remark=this.$t('kwh.normal')
              if(value0){
                  res.result.data[i].remark=this.$t('kwh.status3')
              }
              if(svalue){
                  res.result.data[i].remark=this.$t('kwh.status2')
              }
              if(lvalue){
                  res.result.data[i].remark=this.$t('kwh.status1')
              }
            }         
            return res.result
          })
        }
      }
    },
    methods: {
      showModal(params,type) {
        this.dire=type
        this.params=params
        this.isShow = true
      },
      onSearch() {
        this.$refs.table.refresh()
      },
      onSubmit () {        
        this.isShow = false
      },
      cancel() {
        this.isShow = false
      },
    },
  }
</script>
