<template>
  <a-modal 
    :maskClosable="false" 
    :title="$t('common.detail')" 
    :width="1200" 
    v-model="isShow"
    @ok="onSubmit"
    @cancel="cancel" 
    :destroyOnClose="true">
    <template slot="footer">
      <a-button key="back" @click="cancel" >{{ this.$t('common.btn_cancel') }}</a-button>
    </template>
    <div class="table-page-search-wrapper" style="height:600px">
      <a-form layout="inline">
        <a-row :gutter="20">
          <a-col :md="7" :sm="24">
            <a-form-item :label="this.$t('vee.datatype')">
              <a-select v-model="type">
                <a-select-option value="0">
                  {{ $t('kwh.type0') }}
                </a-select-option>
                <a-select-option value="1">
                  {{ $t('kwh.type1') }}
                </a-select-option>
              </a-select>
            </a-form-item>
          </a-col>
          <a-col :md="7" :sm="24" v-show="type=='0'">
            <a-form-item :label="this.$t('common.time')">
              <pick-range :defaultValue="stratRange" showLevel="1" @change="onChangeTime" />
            </a-form-item>
          </a-col>
          <a-col :md="7" :sm="24" v-show="type=='1'">
            <a-form-item :label="this.$t('common.time')">
              <pick-range :defaultValue="stratRange1" showLevel="2" @change="onChangeTime" />
            </a-form-item>
          </a-col>
          <a-col :md="5" :sm="48" :offset="1">
            <span class="table-page-search-submitButtons">
              <btn-search @click="onSearch" :showInput="false"></btn-search>&nbsp;&nbsp;&nbsp;&nbsp;
              <btn-export @click="exportData" :showInput="false"></btn-export>
            </span>
          </a-col>
        </a-row>
      </a-form>
      <div style="width:60%;float:right">
        <s-table  
          ref="table"
          :bordered="false"
          :autoLoad="true"
          :data="loadData"
          size="small"
          rowKey="id"
        >
          <a-table-column
            :title="this.$t('common.date')"
            width="120px"
            dataIndex="nowTime"
          >
          </a-table-column>
          <a-table-column
            :title="this.$t('loss.supkwh')"
            width="150px"
            dataIndex="supplyKwh"
          >
          </a-table-column>
          <a-table-column
            :title="this.$t('loss.soldkwh')"
            width="150px"
            dataIndex="soldKwh"
          >
          </a-table-column>
          <a-table-column
            :title="this.$t('loss.losskwh')"
            width="150px"
            dataIndex="lossKwh"
          >
          </a-table-column>
          <a-table-column
            :title="this.$t('loss.info3')"
            width="150px"
            dataIndex="lossRate"
          >
          </a-table-column>
        </s-table>
      </div>
      <div style="width:40%;float:right">
        <div style="height:20px;color:#3DB8F5">{{ $t('kwh.kwh') }}</div>
        <div style="height:250px;margin:10px">
          <v-chart
            :forceFit="true"
            height="250"
            :data="data1"
            :scale="scale1"
            padding="auto"
          >
            <v-tooltip crosshairs="y"></v-tooltip>
            <v-axis
              dataKey="value"
              :label="labelFormat"
            >
            </v-axis>
            <v-axis
              dataKey="Date"
              :label="label"
            >
            </v-axis>
            <v-view :data="dv2.rows">
              <v-axis :show="false" dataKey="range"></v-axis>
              <v-area position="Date*range" color="#8d8d8d" :opacity="0.1"></v-area>
            </v-view>
            <v-view :data="dv1.rows">
              <v-point position="Date*value" color="type" :opacity="1" shape="circle"></v-point>
              <v-line position="Date*value" color="type" :opacity="1"></v-line>
            </v-view>
          </v-chart>
        </div>
        <div style="height:20px;color:#3DB8F5">{{ $t('function._38') }}</div>
        <v-chart :force-fit="true" height="250" :data="data" :scale="scale" padding="auto">
          <v-tooltip crosshairs="y"></v-tooltip>
          <v-axis />
       
          <v-line position="nowTime*kWh" color="type" />
          <v-point position="nowTime*kWh" color="type" :size="4" :v-style="style" :shape="'circle'" />
        </v-chart>
      </div>
    </div>
  </a-modal>
</template>

<script>
  import STable from '@/components/table/'
  import { toUTC } from '@/inhe/picker/utils'
  import {
    axios
  } from '@/utils/request'
  const scale = [{
    dataKey: 'nowTime',
  }]
  const DataSet = require('@antv/data-set')
  export default {
    components: {
      STable,
    },
    data() {
      return {
        isShow: false,
        type:'0',
        scale,
        data:[],
        height: 250,
        data1:[],
        label:{},
        scale1:{},
        labelFormat:{},
        dv1:{},
        dv2:{},
        style: { stroke: '#fff', lineWidth: 1 },
        startTime:toUTC(new Date(new Date().getFullYear()+'-'+(new Date().getMonth()+1))),
        endTime: toUTC(new Date()),
        stratRange:[toUTC(new Date(new Date().getFullYear()+'-'+(new Date().getMonth()+1))),toUTC(new Date())],
        startTime1:toUTC(new Date(new Date().getFullYear()+'-01')),
        endTime1: toUTC(new Date()),
        stratRange1:[toUTC(new Date(new Date().getFullYear()+'-01')),toUTC(new Date())],
        loadData:  parameter => { 
          const that = this
          parameter.startTime = that.type=='0'?that.startTime:that.startTime1
          parameter.endTime = that.type=='0'?that.endTime:that.endTime1
          parameter.dataType = that.type
          parameter.refId=that.params.refId   
          parameter.way=that.params.way
          if(parameter.way==3){
            parameter.deptId=that.params.deptId
          }    
          return axios({
           url: '/loss/report/four/search-chart',
            method: 'get',
            params: Object.assign('', parameter)
          }).then(res => { 
            that.getTwoLine(res.result.data)  
            that.getLineData(res.result.data)        
            return res.result
          })
        },
      }
    },
    methods: {
      showModal(params) {
        this.isShow = true
        this.params=params
        this.startTime=toUTC(new Date(new Date().getFullYear()+'-'+(new Date().getMonth()+1)))
        this.endTime=toUTC(new Date())
        this.startTime1=toUTC(new Date(new Date().getFullYear()+'-01'))
        this.endTime1=toUTC(new Date())
      },
      onSubmit () {        
        this.isShow = false
      },
      onSearch(){
        this.$refs.table.refresh()
      },
      showTable(){
        this.showType=!this.showType
      },
      exportData() {        
        const that = this
        const data = {
          isoCode: localStorage.getItem('locale')
        }  
        data.startTime = that.type=='0'?that.startTime:that.startTime1
        data.endTime = that.type=='0'?that.endTime:that.endTime1
        data.dataType = that.type
        data.refId=that.params.refId   
        data.way=that.params.way
        if(data.way==3){
          data.deptId=that.params.deptId
        }         
        axios({
          url: '/loss/report/four/export-chart',
          method: 'get',
          params: Object.assign(data, this.queryParam),
          responseType: 'blob'
        }).then(res => {
          const url = window.URL.createObjectURL(new Blob([res]))
          const link = document.createElement('a')
          link.style.display = 'none'
          link.href = url
          link.setAttribute('download', this.$t('vee.kwh')+'.xls')
          document.body.appendChild(link)
          link.click()          
        })
      },
      onChangeTime(value){
        if(this.type=='0'){
          value instanceof Array?this.startTime=value[0]:this.startTime=''
          value instanceof Array?this.endTime=value[1]:this.endTime=''
        }else{
          value instanceof Array?this.startTime1=value[0]:this.startTime1=''
          value instanceof Array?this.endTime1=value[1]:this.endTime1=''
        }
       
      },
      cancel() {
        this.isShow = false
      },
      getTwoLine(record){
        this.data1=[]
        for (let i = record.length-1; i >=0; i--) {
          this.data1.push({
            Date: record[i].nowTime,
            [this.$t('common.mainmeter')]: record[i].supplyKwh,
            [this.$t('common.lvmeter')]: record[i].soldKwh
          })
        }
        console.log(this.data1)
        const ds = new DataSet()
        this.dv1 = ds.createView().source(this.data1).transform({
          type: 'fold',
          fields: [this.$t('common.mainmeter'), this.$t('common.lvmeter')],
          key: 'type',
          value: 'value',
          retains: ['Date']
        })
        const _this=this
        this.dv2 = ds.createView().source(this.data1).transform({
          type: 'map',
          callback: function callback(row) {
            row.range = [row[_this.$t('common.mainmeter')], row[_this.$t('common.lvmeter')]]
            return row
          }
        })

        this.label = {
          textStyle: {
            fill: '#aaaaaa'
          }
        }
        this.labelFormat = {
        textStyle: {
          fill: '#aaaaaa'
        },
        formatter: function formatter(text) {
            return text.replace(/(\d)(?=(?:\d{3})+$)/g, '$1,')
          }
        }

        this.scale1 = [{
          dataKey:'Date',
          range: [0, 1],
          tickCount: 10,
          type: 'timeCat'
        }]
      },
      getLineData(data) {
        const that = this
        that.data=[]
        const sourceData=[]
        for (let i = data.length-1; i >=0; i--) {
          sourceData.push({
           nowTime:data[i].nowTime,
           [this.$t('loss.info3')]:data[i].lossRate,
          })
          const dv = new DataSet.View().source(sourceData)
          dv.transform({
            type: 'fold',
            fields: [this.$t('loss.info3')],
            key: 'type',
            value: 'kWh',
          })
          that.data = dv.rows
        }
      }
    },
  }
</script>
