<template>
  <div>
    <a-card style="background-color:#fff8db;margin-bottom:10px" v-show="show">
      <div class="table-page-search-wrapper">
        <a-form class="ant-advanced-search-form searchFixed" >
          <a-row :gutter="32">
            <a-col :xl="6" :md="6" :sm="48" style="padding-left:0;">
              <a-form-item :label="this.$t('vee.datatype')">
                <a-select v-model="datetype">
                  <a-select-option value="0">
                    {{ $t('dct.REPEAT_TYPE3') }}
                  </a-select-option>
                  <a-select-option value="1">
                    {{ $t('dct.REPEAT_TYPE4') }}
                  </a-select-option>
                </a-select>
              </a-form-item>
            </a-col>
            <a-col :xl="6" :md="6" :sm="48" style="padding-left:0;" v-show="datetype=='0'">
              <a-form-item :label="$t('common.date')" >
                <pick-date :defaultValue="nowTime" @change="onChangeTime" :allowClear="false"></pick-date>
              </a-form-item>
            </a-col>
            <a-col :xl="6" :md="6" :sm="48" style="padding-left:0;" v-show="datetype=='1'">
              <a-form-item :label="$t('common.date')" >
                <pick-month :defaultValue="nowTime1" @change="onChangeTime" :allowClear="false"></pick-month>
              </a-form-item>
            </a-col>
            <a-col :xl="6" :md="6" :sm="48" style="padding-left:0;">
              <a-form-item :label="this.$t('loss.info5')">
                <dict 
                  type="LINE_RANGE"
                  @change="(value)=>{value?queryParam.code=value[0]:delete queryParam.code}"
                ></dict>
              </a-form-item>
            </a-col>
            <a-col :xl="6" :md="6" :sm="48" style="padding-left:30px;">
              <a-form-item >
                <span>
                  <btn-search @click="queryData" :showInput="false"></btn-search>&nbsp; &nbsp;
                  <a-button @click="reCount" type="primary" ghost>{{ $t('common.recount') }}</a-button>&nbsp; &nbsp;
                  <btn-export @click="exportData" :showInput="false"></btn-export>
                </span>
              </a-form-item>
            </a-col>
          </a-row>
        </a-form>
      </div>
    </a-card>
    <a-card>
      <div class="table-page-search-wrapper" style="margin-bottom: 10px;">
        <a-row>
          <a-col :md="19" :sm="19">
          </a-col>
          <a-col :md="5" :sm="5">
            <sorter @refresh="$refs.table.refresh()" @show="showSearch"></sorter>
          </a-col>
        </a-row>
      </div>
      <s-table 
        ref="table" 
        rowKey="deviceId,nowTime"
        :data="loadData" 
        :bordered="false" 
      > 
        <a-table-column 
          :title="$t('task.index1')"
          width="50px"
          align="center"
        >
          <span slot-scope="text">
            <a-icon type="schedule" theme="twoTone" class="icon" style="font-size:25px"/>
          </span>
        </a-table-column>
        <a-table-column
          :title="this.$t('line.info1')"
          width="120px"
          dataIndex="substationName"
        >
        </a-table-column>
        <a-table-column
          :title="this.$t('line.code1')"
          width="120px"
          dataIndex="lineCode"
        >
        </a-table-column>
        <a-table-column
          :title="this.$t('line.name1')"
          width="150px"
          dataIndex="lineName"
        >
        </a-table-column>
        <a-table-column
          :title="this.$t('common.date')"
          width="120px"
          dataIndex="nowTime"
        >
        </a-table-column>
        <a-table-column 
          :title="$t('loss.supkwh')"
          width="150px"
          dataIndex="supplyKwh"
        >
          <span slot-scope="text,record" >
            <a-button type="link" @click="showDeatil(record,0)">
              {{ record.supplyKwh }}
            </a-button>
          </span>
        </a-table-column>
        <a-table-column 
          :title="$t('loss.soldkwh')"
          width="150px"
          dataIndex="soldKwh"
        >
          <span slot-scope="text,record" >
            <a-button type="link" @click="showDeatil(record,1)">
              {{ record.soldKwh }}
            </a-button>
          </span>
        </a-table-column>
        <a-table-column 
          :title="$t('loss.losskwh')"
          width="150px"
          dataIndex="lossKwh"
        >
        </a-table-column>
        <a-table-column 
          :title="$t('loss.info6')"
          width="150px"
          dataIndex="lossRate"
        >
        </a-table-column>
        <a-table-column 
          :title="$t('loss.info4')"
          width="120px"
          dataIndex="lineLoseIndex"
        >
        </a-table-column>
        <a-table-column 
          :title="$t('loss.info1')"
          width="150px"
          dataIndex="yearBasis"
        >
        </a-table-column>
        <a-table-column 
          :title="$t('loss.info2')"
          width="150px"
          dataIndex="linkRelativeRatio"
        >
        </a-table-column>
        <a-table-column 
          :title="$t('task.action')"
          dataIndex="action"
        >
          <span slot-scope="text,record">
            <a-icon type="line-chart" @click="showChart(record)" :style="{ fontSize: '16px' ,color:'#1890FF'}"/>
          </span>
        </a-table-column>
      </s-table>
    </a-card>
    <show-detail ref="showDetail"></show-detail>
    <dept-select ref="dept" v-show="false"/>
    <show-chart ref="showchart"></show-chart>
  </div>
</template>
<script>
  import STable from '@/components/table/'
  import { axios } from '@/utils/request'
  import Dict from '@/views/public/Dict'
  import { toUTC } from '@/inhe/picker/utils'
  import DeptSelect from '@/views/public/DeptSelect'
  import ShowDetail from '@/views/loss/report/four/ShowDetail'
  import ShowChart from '@/views/loss/report/four/ShowChart'

  export default {
    name: 'Index',
    components: {
      STable,
      Dict,
      DeptSelect,
      ShowDetail,
      ShowChart
    },
    data() {
      return {
        show:true,
        queryParam: {},
        datetype:'0',
        nowTime1:toUTC(new Date(new Date().getTime()-30*24*3600*1000)),
        nowTime: toUTC(new Date(new Date().getTime()-24*3600*1000)),
        loadData:  parameter => { 
          const that = this
          if(that.datetype=='0'){
            parameter.nowTime = that.nowTime.slice(0,10)
          }else{
            parameter.nowTime = that.nowTime1.slice(0,7)
          }
          parameter.way = 4
          parameter.dataType = that.datetype    
          return axios({
            url: '/loss/report/main/search',
            method: 'get',
            params: Object.assign(parameter, this.queryParam)
          }).then(res => { 
            return res.result
          })
        },

      }
    },
    methods: {
      onChangeTime(date){
        date=toUTC(new Date(new Date(date).getTime()+8*3600*1000))
         if(this.datetype=='0'){
           this.nowTime=date
         }else{
           this.nowTime1=date
         }
      },
      showDeatil(record,type){
        this.$refs.showDetail.showModal(record,type)
      },
      showChart(record){
        this.$refs.showchart.showModal(record)
      },
      showSearch() {
        this.show = !this.show
      },
      exportData() {        
        const that = this
        const data = {
          isoCode: localStorage.getItem('locale')
        }  
        if(that.datetype=='0'){
          data.nowTime = that.nowTime.slice(0,10)
        }else{
          data.nowTime = that.nowTime1.slice(0,7)
        }
        data.way = 4
        data.dataType = that.datetype     
        axios({
          url: '/loss/report/main/export',
          method: 'get',
          params: Object.assign(data, this.queryParam),
          responseType: 'blob'
        }).then(res => {
          const url = window.URL.createObjectURL(new Blob([res]))
          const link = document.createElement('a')
          link.style.display = 'none'
          link.href = url
          link.setAttribute('download', this.$t('loss.report0')+'.xls')
          document.body.appendChild(link)
          link.click()          
        })
      },
      reCount(){
        axios({
          url: '/loss/report/four/update-reset',
          method: 'post',
          params: {taskCode:this.datetype=='0'?'008':'009',dataTime:this.datetype=='0'?this.nowTime.slice(0,10):this.nowTime1.slice(0,7)+'-01'},
        }).then(res => {
          if(res.result){
            this.$message.info('Succeeded!')
          }
            
        })
      },
      //输入任务名称的search,上方列表表刷新
      queryData(){
        this.$refs.table.refresh()
      }
    }
  }
</script>
<style lang="scss" scoped>
.headtab{
  background: #fff;
}
.ant-tabs>.ant-tabs-bar{
  margin: 0 !important;
}
.ant-form-item-children .ant-calendar-picker{
  width: 100%;
}
</style>